import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import HeaderContext from "@vkontakte/vkui/dist/components/HeaderContext/HeaderContext";
import List from "@vkontakte/vkui/dist/components/List/List";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import PanelHeaderContent from "@vkontakte/vkui/dist/components/PanelHeaderContent/PanelHeaderContent";
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon24Notification from '@vkontakte/icons/dist/24/notification';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Poll from '@vkontakte/icons/dist/24/poll';
import React from "react";
import FixedLayout from "@vkontakte/vkui/dist/components/FixedLayout/FixedLayout";
import Tabs from "@vkontakte/vkui/dist/components/Tabs/Tabs";
import TabsItem from "@vkontakte/vkui/dist/components/TabsItem/TabsItem";

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rights: props.rights,
            mode: props.mode,
            opened: false,
            client: props.client,
        }
        this.toggleContext = this.toggleContext.bind(this);
    }

    toggleContext() {
        this.setState({opened: !this.state.opened});
    }

    render() {
        if (this.state.rights === "editor" || this.state.rights === "admin") {
            if (this.state.client !== 'desktop_web') {
                console.log(this.props.client);
                return (
                    <PanelHeader noShadow>
                        <PanelHeaderContent aside={<Icon16Dropdown/>} onClick={this.toggleContext}>
                            {this.state.mode === 'subscribe' ? 'Уведомления' : null}
                            {this.state.mode === 'statistic_full' ? 'Статистика' : null}
                            {this.state.mode === 'statistic_cur' ? 'Текущий сезон' : null}
                        </PanelHeaderContent>
                        <HeaderContext opened={this.state.opened} onClose={this.toggleContext}>
                            <List>
                                <Cell
                                    before={<Icon24Notification/>}
                                    asideContent={this.state.mode === 'subscribe' ? <Icon24Done fill="var(--accent)"/> :
                                        <div style={{width: '24px'}}/>}
                                    onClick={(e) => {
                                        this.setState({opened: !this.state.opened});
                                        this.props.select(e);
                                    }}
                                    data-mode="subscribe"
                                >
                                    Уведомления
                                </Cell>
                                <Cell
                                    before={<Icon24Poll/>}
                                    asideContent={this.state.mode === 'statistic_full' ?
                                        <Icon24Done fill="var(--accent)"/> : <div style={{width: '24px'}}/>}
                                    onClick={(e) => {
                                        this.setState({opened: !this.state.opened});
                                        this.props.select(e);
                                    }}
                                    data-mode="statistic_full"
                                >
                                    Статистика
                                </Cell>
                                <Cell
                                    before={<Icon24Poll/>}
                                    asideContent={this.state.mode === 'statistic_cur' ?
                                        <Icon24Done fill="var(--accent)"/> : <div style={{width: '24px'}}/>}
                                    onClick={(e) => {
                                        this.setState({opened: !this.state.opened});
                                        this.props.select(e);
                                    }}
                                    data-mode="statistic_cur"
                                >
                                    Текущий сезон
                                </Cell>

                            </List>
                        </HeaderContext>
                    </PanelHeader>
                );
            } else {
                return (
                    <div>
                        <PanelHeader noShadow>
                            {this.state.mode === 'subscribe' ? 'Уведомления' : null}
                            {this.state.mode === 'statistic_full' ? 'Статистика' : null}
                            {this.state.mode === 'statistic_cur' ? 'Текущий сезон' : null}
                        </PanelHeader>
                        <FixedLayout vertical="top">
                            <Tabs>
                                <TabsItem selected={this.state.mode === 'subscribe'} data-mode="subscribe" onClick={this.props.select}>Уведомления</TabsItem>
                                <TabsItem selected={this.state.mode === 'statistic_full'} data-mode="statistic_full" onClick={this.props.select}>Статистика</TabsItem>
                                <TabsItem selected={this.state.mode === 'statistic_cur'} data-mode="statistic_cur" onClick={this.props.select}>Текущий сезон</TabsItem>
                            </Tabs>
                        </FixedLayout>
                    </div>
                    );

            }


        } else {
            return (<PanelHeader noShadow>Уведомления</PanelHeader>);

        }

    }
}


export default Header;